export default {
  data() {
    return {};
  },
  onLoad() {
    console.log(this.$Route);
    this.randomMusic();
  },
  methods: {
    goCenter() {
      this.$Router.pushTab({ name: "center" });
    },
    async randomMusic() {
      let { data: res } = await this.$api.random_music({
        params: {
          sort: "热歌榜",
          format: "json"
        }
      });
      console.log(res);
    }
  }
};
